import React, { Component } from 'react';
import './component.css';

function sendEmail() {
  var email = document.getElementById("email");
  email = email.value;
  var message = document.getElementById("message");
  message = message.value;
  var mail = document.createElement('a');
  mail.href = "mailto:cameronpavao@gmail.com?subject=From "+ email + "&body=" + message;
  mail.click();
  console.log(email, message)
}

class Contact extends Component {
  render() {
    return (
      <div className="form">
      
        <div className="mx-auto" style={{width: '200'}} >
        <h3>The easiest way to contact me is by email! Send your message below</h3>
        <br></br>
        <br></br>

        <form>
          <fieldset>
          <div className="form-group">
            <label htmlFor="email">Your Email address</label>
            <input type="email" className="form-control" id="email" aria-describedby="emailHelp" placeholder="Enter email" />
          </div>
          <div className="form-group">
            <label htmlFor="message">Your Message</label>
            <textarea className="form-control" type='text' id="message" rows="3" placeholder="Enter your message"></textarea>
          </div>
          <button type="submit" className="btn btn-primary" onClick={sendEmail}>Submit</button>
          </fieldset>
        </form>
        </div>
        </div>
    );
  }
}

export default Contact;
